export default "import { DragDropModule } from \"@angular/cdk/drag-drop\";\nimport { NgModule } from \"@angular/core\";\nimport { FormsModule, ReactiveFormsModule } from \"@angular/forms\";\nimport { RouterModule } from \"@angular/router\";\nimport {\n    DEMO_PATH_TOKEN,\n    NuiButtonModule,\n    NuiCheckboxModule,\n    NuiCommonModule,\n    NuiDialogModule,\n    NuiDocsModule,\n    NuiExpanderModule,\n    NuiIconModule,\n    NuiMenuModule,\n    NuiMessageModule,\n    NuiOverlayAdditionsModule,\n    NuiOverlayModule,\n    NuiSearchModule,\n    NuiSelectV2Module,\n    SrlcStage\n} from \"@nova-ui/bits\";\n\nimport {\n    CustomConfirmationInsideDialogComponent,\n    OverlayArrowExampleComponent,\n    OverlayCustomContainerExampleComponent,\n    OverlayCustomDialogComponent,\n    OverlayCustomStylesExampleComponent,\n    OverlayDocsComponent,\n    OverlayPopupStylesExampleComponent,\n    OverlayShowHideToggleExampleComponent,\n    OverlaySimpleExampleComponent,\n    OverlayTestComponent,\n    OverlayViewportMarginExampleComponent,\n} from \"./index\";\n\nconst routes = [\n    {\n        path: \"\",\n        component: OverlayDocsComponent,\n        data: {\n            \"srlc\": {\n                stage: SrlcStage.beta,\n            },\n            showThemeSwitcher: true,\n        },\n    },\n    {\n        path: \"basic\",\n        component: OverlaySimpleExampleComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n    {\n        path: \"with-popup-styles\",\n        component: OverlayPopupStylesExampleComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n    {\n        path: \"custom-styles\",\n        component: OverlayCustomStylesExampleComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n    {\n        path: \"arrow\",\n        component: OverlayArrowExampleComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n    {\n        path: \"custom-container\",\n        component: OverlayCustomContainerExampleComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n    {\n        path: \"toggle-examples\",\n        component: OverlayShowHideToggleExampleComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n    {\n        path: \"viewport-margin\",\n        component: OverlayViewportMarginExampleComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n    {\n        path: \"custom-dialog\",\n        component: OverlayCustomDialogComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n    {\n        path: \"custom-confirmation-dialog\",\n        component: CustomConfirmationInsideDialogComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n    {\n        path: \"overlay-test\",\n        component: OverlayTestComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n];\n\n@NgModule({\n    imports: [\n        NuiCommonModule,\n        NuiSelectV2Module,\n        NuiOverlayModule,\n        NuiOverlayAdditionsModule,\n        NuiMessageModule,\n        NuiButtonModule,\n        NuiExpanderModule,\n        NuiDialogModule,\n        NuiIconModule,\n        ReactiveFormsModule,\n        FormsModule,\n        NuiCheckboxModule,\n        NuiSearchModule,\n        NuiDocsModule,\n        NuiMenuModule,\n        DragDropModule,\n        RouterModule.forChild(routes),\n    ],\n    declarations: [\n        OverlaySimpleExampleComponent,\n        OverlayShowHideToggleExampleComponent,\n        OverlayTestComponent,\n        OverlayCustomStylesExampleComponent,\n        OverlayCustomContainerExampleComponent,\n        OverlayDocsComponent,\n        OverlayViewportMarginExampleComponent,\n        OverlayArrowExampleComponent,\n        OverlayPopupStylesExampleComponent,\n        OverlayCustomDialogComponent,\n        CustomConfirmationInsideDialogComponent,\n    ],\n    providers: [\n        {\n            provide: DEMO_PATH_TOKEN,\n            useFactory: () => (<any>require).context(`!!raw-loader!./`, true, /.*\\.(ts|html|less)$/),\n        },\n    ],\n    exports: [\n        RouterModule,\n    ],\n})\nexport class OverlayModule {\n}\n";